/**
 * 
 * userNo 
 * pageNum 
 * pageSize 
 * visitorName 
 *  */

export const getVisitorAllByVisitorNameRequest = async (pageNum,pageSize,visitorName) => $http.post(`${BASE_URL}/visitorcontrol/SanyBussVisitor/getVisitorAllByVisitorName`, {
  'pageNum': pageNum,
  'pageSize': pageSize,
  'query': {
    'visitorName': visitorName
  }
})

