export const checkPhone = (tel,h) => {
  console.log('tel:',tel)
  if(tel.length === 11){
    if(!(/^1[3456789]\d{9}$/.test(tel))){
      // console.log('this:',h)
      h.$message({
        type:'error',
        message:''
      })
    }
  }else {
    h.$message({
      type:'error',
      message:'11'
    })
  }
}
// 
export function checkIDCard (IDNumber,h) {
  if(IDNumber.length === 15){
    if(!/(^\d{15}$)/.test(IDNumber)){
      h.$message({
        type:'error',
        message:''
      })
    }
  }else if(IDNumber.length === 18){
    if(!/(^\d{17}([0-9]|x|X)$)/.test(IDNumber)){
      h.$message({
        type:'error',
        message:''
      })
    }
  }else{
    h.$message({
      type:'error',
      message:'1518'
    })
  }
}
// 
export function checkCarCard (CarCardNum,h) {
  var PTNumExpress = /^[A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9]{1}$/;
  var XNYumExpress = /^[A-Z]{1}[A-Z]{1}(([0-9]{5}[DF]$)|([DF][A-Z0-9][0-9]{4}$))/;
  if(CarCardNum.length === 7){
    if(!(PTNumExpress.test(CarCardNum))){
      h.$message({
        type:'error',
        message:''
      })
    }
  }else if(CarCardNum.length === 8){
    if(!(XNYumExpress.test(CarCardNum))){
      h.$message({
        type:'error',
        message:''
      })
    }
  }else{
    h.$message({
      type:'error',
      message:'78'
    })
  }
}


