                           :http-request="uploadSectionFile" :auto-upload="false">
                  <el-button id="upload-document" slot="trigger" type="primary" v-show="isShow"></el-button>
                  <el-button style="width: 100px;" type="primary" @click="submitUpload" v-show="!isShow"></el-button>
                </el-upload>

              </el-form-item>
              <el-form-item>
                <!--<el-button type="primary"></el-button>-->
                <el-upload id="formImage" style="display:inline-block;background:none" :limit="1"
                           class="upload-demo" ref="uploadImage" accept=".zip"  action="/userPushRecord/find/all"
                           :on-change="beforeUploadImages" :on-exceed="handleExceedImages"
                           :on-remove="removeImages"
                           :before-upload="onBeforeUploadImage"
                           :http-request="uploadSectionImageFile" :auto-upload="false">
                  <el-button id="upload-images" slot="trigger" type="primary" v-show="isShowImage"></el-button>
                  <el-button style="width: 100px;" type="primary" @click="submitUploadImages" v-show="!isShowImage"></el-button>
                </el-upload>

              </el-form-item>
              <br />
              <el-form-item>
                <el-button type="primary" @click="handleAdd"></el-button>
              </el-form-item>
          </el-form>
          <div class="common-table">
