              </el-table-column>
              <el-table-column
                prop="sanyBasicShrOrg.orgText"
                label="">
              </el-table-column>
              <el-table-column
                prop="userName"
                label="">
              </el-table-column>
              <el-table-column
                prop="telephone"
                label="">
              </el-table-column>
              <el-table-column
                prop="idCard" width="170"
                label="">
              </el-table-column>
              <el-table-column prop="isFa" label="">
                <template slot-scope="scope">
                  <el-text v-show="scope.row.isSub==='1'" size="mini" type="text"></el-text>
                  <el-text v-show="scope.row.isSub==='2'" size="mini" type="text"></el-text>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-text v-show="scope.row.imgVerify==='1'" size="mini" type="text"></el-text>
                  <el-text v-show="scope.row.imgVerify==='2'" size="mini" type="text"></el-text>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button v-if="scope.row.imgVerify!=='0'"
                    size="mini"
                    type="text"
                    @click="handleLookPhoto(scope.$index, scope.row)"></el-button>
                  <el-text v-else></el-text>
                </template>
              </el-table-column>
              <!--<el-table-column label="">
                <template slot-scope="scope">
                  &lt;!&ndash;&ndash;&gt;
                  <el-button disabled
                    size="mini"
                    type="text"
                    @click="handleLookCardPhoto(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>-->
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="text" @click="handleEditOuterWorkerManage(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>

            </el-table>
          </div>
          <div class="marginTop10 text-right">
            <el-pagination
