        <el-form :model="outWorkerEditForm"
                 :rules="editRules"
                 size="medium"
                 label-width="100px"
                 class="marginTop20 common-form-inline addVisitorDialog_form editWorkerMaganage"
                 ref="workerManageList">

            <el-form-item label="" prop="userName">
              <el-input v-model="outWorkerEditForm.userName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="telephone">
              <el-input v-model="outWorkerEditForm.telephone" placeholder="" @change="changeTelephoto"></el-input>
            </el-form-item>
